import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import InstituteFinanceManagerSide from "./InstituteFinanceManagerSide";
import { useFinanceDashboardMaster } from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import InstituteFinanceManagerProfile from "./InstituteFinanceManagerProfile";
import Loading from "../../Loader/Loading";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useAdmissionDashboardMaster } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const BankDetail = lazy(() =>
  import("../../UserStaffMember/FinanceManager/BankDetail/BankDetail")
);

const PendingFees = lazy(() =>
  import("../../UserStaffMember/FinanceManager/PendingFees/PendingFees")
);
const Income = lazy(() =>
  import("../../UserStaffMember/FinanceManager/Income/Income")
);
const Expense = lazy(() =>
  import("../../UserStaffMember/FinanceManager/Expense/Expense")
);
const ExemptionUnrecovered = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/ExemptionUnrecovered/ExemptionUnrecovered"
  )
);
const FinanceExport = lazy(() =>
  import("../../UserStaffMember/FinanceManager/FinanceExport/FinanceExport")
);
const TransactionHistory = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/TransactionHistory/TransactionHistory"
  )
);
const FinanceModerator = lazy(() =>
  import("../../UserStaffMember/FinanceManager/Moderator/FinanceModerator")
);
const FinanceDeposit = lazy(() =>
  import("../../UserStaffMember/FinanceManager/FinanceDeposit/FinanceDeposit")
);
// const StudentStatitics = lazy(() =>
//   import(
//     "../../UserStaffMember/FinanceManager/StudentStatitics/StudentStatitics"
//   )
// );
const StudentStatiticsDetail = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/StudentStatitics/StudentStatiticsDetail"
  )
);
const InternalFees = lazy(() =>
  import("../../UserStaffMember/FinanceManager/InternalFees/InternalFees")
);
const FeeStatistics = lazy(() =>
  import("../../UserStaffMember/FinanceManager/FeeStatistics/FeeStatistics")
);

const Scholarship = lazy(() =>
  import("../../UserStaffMember/FinanceManager/Scholarship/Scholarship")
);
const InternalFeesDetail = lazy(() =>
  import("../../UserStaffMember/FinanceManager/InternalFees/InternalFeesDetail")
);
const InstituteFinanceTabController = lazy(() =>
  import("./InstituteFinanceTabController")
);

const AdmissionFeeStatistics = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/FeeStatistics/AdmissionStatistics/AdmissionFeeStatistics"
  )
);
const AdmissionPendingFeesConfig = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/PendingFees/AdmissionPendingFeesConfig"
  )
);
const PaymentVerification = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/PaymentVerification/PaymentVerification"
  )
);
const ExcessFees = lazy(() =>
  import("../../UserStaffMember/AdmissionManager/ExcessFees/ExcessFees")
);
const PendingFeesReminderMessageHistory = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/PendingFees/PendingFeesReminderMessageHistory"
  )
);
const FinanceSetting = lazy(() =>
  import("../../UserStaffMember/FinanceManager/FinanceSetting/FinanceSetting")
);
const FinanceOtherFees = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/FinanceOtherFees/FinanceOtherFees"
  )
);
const FinanceOtherFeesDetail = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/FinanceOtherFees/FinanceOtherFeesDetail/FinanceOtherFeesDetail"
  )
);
const FinanceDaybook = lazy(() =>
  import("../../UserStaffMember/FinanceManager/FinanceDaybook/FinanceDaybook")
);

const InstituteFinanceManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { financeDashboardMaster, financeDashboardMasterLoading } =
    useFinanceDashboardMaster({
      data: {
        fid: getQuery.state?.fid,
        mid: "",
      },
      skip: !getQuery.state?.fid,
    });
  const { admissionDashboardMaster } = useAdmissionDashboardMaster({
    data: {
      aid: financeDashboardMaster?.finance?.institute?.admissionDepart?.[0],
      mid: "",
    },
    skip: !financeDashboardMaster?.finance?.institute?.admissionDepart?.[0],
  });
  //   console.info("sfbdbnmsfs", financeDashboardMaster);
  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <InstituteFinanceManagerSide
          financeDashboardMaster={financeDashboardMaster?.finance}
        />
        <div>
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("type") ||
            ["fees"]?.includes(searchParams.get("type")) ? (
              <InstituteFinanceManagerProfile
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
                categoryCount={
                  financeDashboardMaster?.finance?.fees_category_count ?? 0
                }
                masterCount={
                  financeDashboardMaster?.finance?.fee_master_array_count ?? 0
                }
                tabManage={financeDashboardMaster?.finance?.tab_manage}
                financeData={financeDashboardMaster?.finance}
                hostelId={
                  financeDashboardMaster?.finance?.institute?.hostelDepart?.[0]
                }
              />
            ) : searchParams.get("type") === "bank" ? (
              <BankDetail
                fid={getQuery.state?.fid}
                libraryId={
                  financeDashboardMaster?.finance?.institute?.library?.[0]
                }
                transportId={
                  financeDashboardMaster?.finance?.institute
                    ?.transportDepart?.[0]
                }
                hostelId={
                  financeDashboardMaster?.finance?.institute?.hostelDepart?.[0]
                }
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "pending-fee" ? (
              <PendingFees fid={getQuery.state?.fid} />
            ) : searchParams.get("type") === "income" ? (
              <Income fid={getQuery.state?.fid} />
            ) : searchParams.get("type") === "expense" ? (
              <Expense fid={getQuery.state?.fid} />
            ) : searchParams.get("type") === "exemption" ? (
              <ExemptionUnrecovered
                fid={getQuery.state?.fid}
                exemptBalance={
                  financeDashboardMaster?.finance?.financeExemptBalance
                }
              />
            ) : searchParams.get("type") === "export" ? (
              <FinanceExport
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
                hostelId={
                  financeDashboardMaster?.finance?.institute?.hostelDepart?.[0]
                }
              />
            ) : searchParams.get("type") === "history" ? (
              <TransactionHistory
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "moderator" ? (
              <FinanceModerator
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "deposit" ? (
              <FinanceDeposit
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : // searchParams.get("type") === "student-statistics" ? (
            //   <StudentStatitics fid={getQuery.state?.fid} />
            // ) :
            searchParams.get("type") === "statistics-detail" ? (
              <StudentStatiticsDetail />
            ) : searchParams.get("type") === "internal-fees" ? (
              <InternalFees fid={getQuery.state?.fid} />
            ) : searchParams.get("type") === "fees-statistics" ? (
              <FeeStatistics
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "admission-statistics" ? (
              <AdmissionFeeStatistics
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "scholarship" ? (
              <Scholarship
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
                aid={
                  financeDashboardMaster?.finance?.institute
                    ?.admissionDepart?.[0]
                }
                studentAlias={
                  financeDashboardMaster?.finance?.institute
                    ?.alias_pronounciation?.student_alias
                }
              />
            ) : searchParams.get("type") === "admission-fee" ? (
              <AdmissionPendingFeesConfig
                aid={
                  financeDashboardMaster?.finance?.institute
                    ?.admissionDepart?.[0]
                }
                instituteId={financeDashboardMaster?.finance?.institute?._id}
                studentAlias={
                  financeDashboardMaster?.finance?.institute
                    ?.alias_pronounciation?.student_alias
                }
                fid={getQuery.state?.fid}
              />
            ) : searchParams.get("type") === "verification" ? (
              <PaymentVerification
                aid={
                  financeDashboardMaster?.finance?.institute
                    ?.admissionDepart?.[0]
                }
                instituteId={financeDashboardMaster?.finance?.institute?._id}
                requestedCount={
                  admissionDashboardMaster?.fee_receipt_request_count
                }
                approvedCount={
                  admissionDashboardMaster?.fee_receipt_approve_count
                }
                rejectCount={admissionDashboardMaster?.fee_receipt_reject_count}
              />
            ) : searchParams.get("type") === "excess" ? (
              <ExcessFees
                instituteId={financeDashboardMaster?.finance?.institute?._id}
                totalRefund={admissionDashboardMaster?.refundCount}
                totalRefunded={admissionDashboardMaster?.refundedCount}
                aid={
                  financeDashboardMaster?.finance?.institute
                    ?.admissionDepart?.[0]
                }
              />
            ) : searchParams.get("type") === "reminder-message" ? (
              <PendingFeesReminderMessageHistory
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "internal-fee-detail" ? (
              <InternalFeesDetail fid={getQuery.state?.fid} />
            ) : searchParams.get("type") === "tab" ? (
              <InstituteFinanceTabController financeId={getQuery.state?.fid} />
            ) : searchParams.get("type") === "setting" ? (
              <FinanceSetting
                fid={getQuery.state?.fid}
                masterData={financeDashboardMaster?.finance}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "other-fees" ? (
              <FinanceOtherFees
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "other-fees-detail" ? (
              <FinanceOtherFeesDetail
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : searchParams.get("type") === "daybook" ? (
              <FinanceDaybook
                fid={getQuery.state?.fid}
                instituteId={financeDashboardMaster?.finance?.institute?._id}
              />
            ) : null}
          </Suspense>
          {financeDashboardMasterLoading && <Loading />}
        </div>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteFinanceManager;
